 .home_container{
    width: 100%;
    margin-top: 80px;
    min-height: 200vh;
    overflow: hidden;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    flex-direction: column;
}
.home_new_container{
    max-height: 800px;
    width: 1318px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: row;
}
.home_new_presentation{
    width: 430px;
    height: 100%;
    min-width: 430px;
    overflow: hidden;
    display: flex;
    align-items: flex-start;
    flex-direction: column;
    padding: 19px 0;
}
.home_moving_background{
     width: 200vw;
     height: 140px;
     right: 0;
     top: 0;
     background: linear-gradient(155deg, rgba(244, 67, 54, 1) 0%, rgba(233, 30, 99, 1) 7%, rgba(156, 39, 176, 1) 15%, rgba(63, 81, 181, 1) 24%, rgba(33, 150, 243, 1) 33%, rgba(3, 169, 244, 1) 41%, rgba(0, 188, 212, 1) 49%, rgba(0, 150, 136, 1) 58%, rgba(76, 175, 80, 1) 66%, rgba(139, 192, 74, 1) 75%, rgba(205, 220, 57, 1) 84%, rgba(255, 235, 59, 1) 92%, rgba(255, 193, 7, 1) 100%);
     animation: gradient-animation 5s linear infinite;
}
@keyframes gradient-animation {
   0%{
    transform: translateX(0%);
   }
   50%{
    transform: translateX(-160vw);
   }
   100%{
    transform: translateX(0%);
   } 
}
.home_new_title{
    width: 50%;
    height: 140px;
    min-width: 556px;
    background-color: white;
    mix-blend-mode: screen;
    z-index: 10;
    position: absolute;
}
.home_new_title > h1,
.home_new_title > h2{
    font-family:"Roboto";
    font-size: 80px;
    font-weight: 700;
    line-height: 70px;
    letter-spacing: 1px;
}
.home_video{
    /*  */
    z-index: 11;
}
.home_video > video{
    width:auto;
    height: 500px;
    z-index: 11;
}
.home_new_presentation > p{
   font-family:"Roboto";
   max-width: 500px;
   margin: 35px 0;
   font-size: 23px;
   letter-spacing: 0.3px;
   color: #777777;
   font-weight: 400;
}
.home_new_btns_container{
    width: 100%;
    display: flex;
    flex-direction: column;
}
.home_new_btns_container > a{
    text-decoration: none;
    background-color: #0082e6;
    padding: 15px 20px;
    max-width: 70%;
    color: white;
    font-family: "Roboto";
    font-size: 20px;
    letter-spacing: 0.3px;
    cursor: pointer;
    border-radius: 15px;
    display: flex;
    align-items: center;
    justify-content: flex-start;
}
.home_new_btns_container > a:hover{
    background:linear-gradient(to right, #0064e6, #0082e6);
}
.home_new_btns_container > a:focus{
    outline: none;
}
.home_new_btns_container > a:first-child{
    margin-bottom: 8px;
}
/* logos section */
.home_logos{
  width:1318px;
  margin: 4rem;
}
.home_logos > h1{
    font-family: "Roboto";
    font-weight: 400;
    font-size: 50px;
    color: #222222;
}
.home_logos > p:nth-child(2){
     font-size: 20px;
     font-family: "Roboto";
     letter-spacing: 0.2px;
     font-weight: 400
}
.home_logos > p:nth-child(3){
    font-family: "Roboto";
    font-size: 18px;
    color: #666666;
}
.home_logos > div{
   display: flex;
   align-items: flex-start;
   justify-content: flex-start;
   flex-direction: row;
   width: 100%;
   margin: 2rem 0;
}
.home_logos > div > img{
   object-fit: contain;
   margin: 0 15px; 
   width: 300px;
   height: 300px;
   min-width: 50px;
   min-height: 50px;
   border-radius: 150px;
   box-shadow: 0 0 3px rgb(212, 212, 212);
}
.home_logos > div > img:first-child{
    margin: unset !important;
}
.home_logos > div > img:nth-child(2){
    margin-left:30px;
}
.home_logos > a{
    text-decoration: none;
}
.home_logos > a > button{
    width: 350px;
    max-width: 350px;
    font-family: "Roboto";
    color: white;
    background-color: var(--blue);
    padding: 15px 30px;
    border: none;
    font-size: 18px;
    font-weight: 500;
    letter-spacing: 0.1px;
    border-radius: 10px;
    letter-spacing: 0.3px;
}
.home_logos > a > button:hover{
   background-color: #0274cb;
}
.home_websites_interactions{
  width: 100%;
  background-color: #222222;
}


@media screen and (min-width: 1700px){
    .home_new_container{
        width: 1636px;
    }
    .home_new_presentation{
        width: 520px;
    }
    .home_moving_background{
      height: 180px;
    }
    .home_new_title{
        height: 180px;
    }
    .home_new_title > h1,
    .home_new_title > h2{
    font-size: 100px;
    line-height: 90px;
    letter-spacing: -1px;
    }
    .home_new_presentation > p{
        font-size: 29px;
        letter-spacing: 0.5px;
    }
    .home_new_btns_container > a{
        padding: 18px 22px;
        font-size: 24px;
        letter-spacing: 0.5px;
    }
    .home_video{
        z-index: 11;
    }
    .home_video > video{
        width:auto;
        height: 628px;
    }
    /* branding */
     .home_logos{
        width: 1636px;
        margin: 6rem 0;
     }
     .home_logos > h1{
        font-size: 70px;
     }
     .home_logos > p:nth-child(2),
     .home_logos > p:nth-child(3){
        font-size: 29px;
     }
     .home_logos > div > img{
        width: 24%;
        height: 24%;
       border-radius: 50%
     }
     .home_logos > a > button{
        padding: 18px 22px;
        font-size: 24px;
        letter-spacing: 0.5px;
     }
}

@media screen and (max-width: 1350px){
     .home_new_container{
        width: 100%;
        padding: 0 2rem;
     }
    .home_logos{
    width: 100%;
    padding: 0 2rem;
    }
    .home_logos > div > img{
        width: calc(25% - 25px);
        height: calc(25% - 25px);
    }
}
/* de aici esta codul vechi */
@media screen and (max-width:970px){
    .home_new_container{
        display: flex;
        align-items: center;
        justify-content: start;
        flex-direction: column;
        max-height: unset;
    }
    .home_new_presentation{
        width: 100%;
        height: 100%; 
        min-height: unset;
        max-height: unset;
        display: flex;
        align-items: center;
        flex-direction: column;
    }
    .home_new_title{
       width: 100%;
       min-width: unset;
    }
    .home_new_title > h1,
    .home_new_title > h2,    
    .home_new_presentation > p{
        text-align: center;
    }
    @keyframes gradient-animation {
        0%{
         transform: translateX(35%);
        }
        50%{
            transform: translateX(-35%);
        }
        100%{
         transform: translateX(35%);
        }  
     }
     .home_new_btns_container{
        width: 100%;
        display: flex;
        align-items: center;
        flex-direction: column;
     }
     .home_new_btns_container > a{
        width: 350px;
        display: flex;
        align-items: center;
        justify-content: center;
    }
    .home_video{
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        margin-top: 20px;
    }
    .home_video > video{
        width: 100%;
        height: auto;
    }
    /* branding */
    .home_logos{
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
    }
    .home_logos > div{
        height: 375px;
        width: 375px;
        display: grid;
        grid-template-columns: 1fr 1fr;
        grid-template-rows: 1fr 1fr;
       color: green;
       align-items: center;
       justify-items: center;
       gap: 15px;

    }
    .home_logos > div > img {
        width: 100%;
        height: 100%;
    }
    .home_logos > div > img{
        margin: 0;
        margin-left: 0 !important
    }
}
@media screen and (max-width:700px){
     @keyframes gradient-animation {
        0%{
         transform: translateX(-50vw);
        }
        50%{
            transform: translateX(50vw);
        }
        100%{
         transform: translateX(-50vw);
        } 
     } 
}
@media screen and (max-width:450px){
    .home_new_presentation{
        min-width: unset;
    }
    .home_container{
      margin-top: 60px;
    }
    .home_new_container{
        padding: 10px;
    }
    .home_moving_background{
        height: 100px;
      }
      .home_new_title{
          height: 100px
      }
      .home_new_title > h1,
      .home_new_title > h2{
      font-size: 60px;
      line-height: 50px;
      letter-spacing: -2px;
      }
      .home_new_presentation > p{
        font-size: 18px;
        letter-spacing: 0.4px;
        line-height: 27px;
      }
      .home_new_btns_container > a{
       font-size: 18px;
       max-width: 50%;
       padding: 14px 20px;
      }
      .home_video{
        width: 140vw;
      }
      /* branding */
      .home_logos{
        /* margin: 0; */
        padding: 0;
      }
      .home_logos > h1{
        font-size: 35px;
        width: 100%;
        text-align: center;
      }
      .home_logos >p:nth-child(2){
        font-size: 17px;
        width: 100%;
        text-align: center;
      }
      .home_logos >p:nth-child(3){
        font-size: 16px;
        width: 100%;
        text-align: center;
      }
      .home_logos > a > button{
        padding: 14px 49px;
        font-size: 18px;
        border-radius: 15px;
        max-width: 50vw;
       }
} 
@media screen and (max-width:375px){
    .home_logos{
        margin: 0;
        padding: 0;
        width: 100%;
    }
    .home_logos > div{
        width: 95%;
        height: 95%;
    }
}



